/** @format */

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton {
        width: 3rem;
        margin-top: 3rem;
        margin-bottom: 3rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }

        @include tablet {
            margin-top: 0;
        }
    }
}

.svg-container {
    width: 100%;

    .svg {
        background-image: url(../../../build/img/logo.svg);
        background-repeat: no-repeat;
        background-position: center center;
        width: 100%;

        height: 5rem;

        margin: 0 auto;

        @include tablet {
            width: 30rem;
            margin: 0;
        }
    }
}

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion {
    height: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 3s ease-in-out, opacity 0.5s linear;

    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}

// Men√∫ de hamburguesa

.mobile-menu {
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}
