.testimonial {
	background-color: $verde;
	font-size: 2.4rem;
	padding: 2rem;
	color: $blanco;
	border-radius: 2rem;

	blockquote {
		padding-left: 5rem;
		position: relative;

		&::before {
			content: "";
			background-image: url(../img/comilla.svg); // no usamos web p ya que svg es un
			// formato muy utilizado y muy ligero
			background-repeat: no-repeat; // usamos -> no-repat -> ya que por default todas las imagenes se repiten
			width: 6rem;
			height: 8rem;
			display: block;
			position: absolute;
			left: -2rem;
		}
	}
	p {
		text-align: right;
		color: $blanco;
	}
}
