/** @format */

@include tablet {
    .contenedor-anuncios {
        @include grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    // La funci√≥n lighten() se utiliza para aumentar el valor de luminosidad de un color especificado. En este caso, la variable $gris se est√° utilizando como el color base y se est√° aplicando un incremento de luminosidad del 5%, lo que resultar√° en un tono m√°s claro de gris.
    border: 1px solid darken($gris, 10);
    margin-bottom: 2rem;

    .contenido-anuncio {
        padding: 2rem;
    }

    h3,
    p {
        margin: 0;
    }
}
.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1; // es equivalente a flex-grow: 1;
        display: flex;

        img {
            flex: 0 0 3rem;
            // flex para controlar el tama√±o del elemento de imagen img.
            margin-right: 1rem;
        }
        p {
            font-weight: $black;
        }
    }
}
