/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) { // 768px 
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgcolor, $display) {
    background-color: $bgcolor;
    color: $blanco;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center; 
    // margin-top: $separacion / 2; 
    margin-top: calc(#{$separacion} / 2);
    display: $display;
    border: none;
    &:hover {
        background-color: darken($bgcolor, 5);
        cursor: pointer;
    }
}