@include tablet {
	.seccion-inferior {
		display: grid;
		grid-template-columns: 2fr 1fr;
		column-gap: 2rem;
	}
}

.entrada-blog {
	// Selector de clase
	margin-bottom: 2rem;

	@include tablet {
		display: grid;
		grid-template-columns: 1fr 2fr;
		column-gap: 2rem;
	}
	&:last-of-type {
		margin-bottom: 0;
	}
}
.informacion-meta{

	span {
		color: $amarillo;
	}

}

.texto-entrada {
	a {
		color: $negro;
		//margin-top: 10rem -> Esta propiedad no sera √∫til ya que los enlaces tienen un display: inline
		//y al ser de tipo inline no le puedes poner un margin
		margin-top: 2rem;
		display: block;
		@include tablet {
			margin-top: 0;
		}
	}
	h4 {
		margin: 0;
		line-height: 1.4;

		&::after {
			content: ""; // para que funcione el pseudo elemento tiene que existir content con dos comillas vac√≠as
			// Todo lo que pongamos a parte del content, es exactamente lo mismo que si pusieramos un span
			display: block;
			width: 15rem;
			height: 0.5rem;
			background-color: $verde;
			margin-top: 1rem;
		}
	}

}
